import { BackProvider } from './../../providers/back/back';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, ViewController, ModalController, AlertController } from 'ionic-angular';
import { Observable } from 'rxjs';
import { Exam, Collections, Question } from '../../app/app.models';
import { AngularFirestore } from 'angularfire2/firestore';
import { tap } from 'rxjs/operators';
import moment from 'moment';

import * as countdown from 'countdown';
countdown.setLabels(
  ' milissegundo| segundo| minuto| hora| día| semana| mes| año| decada| siglo| milenio',
  ' milisegundos| segundos| minutos| horas| días| semanas| meses| años| décadas| siglos| milenios',
  ' ',
  ' ',
  'ahora');

@IonicPage({segment: 'examen/:type/:id'})
@Component({
  selector: 'page-examen-detail',
  templateUrl: 'examen-detail.html',
})
export class ExamenDetailPage {

  public id: string = this.navParams.get('id')
  public type: string = this.navParams.get('type')
  private isOffline: boolean = this.navParams.get('offline')

  public exam$: Observable<Exam>

  public test: any
  public l: boolean = false

  public index: number = 0

  private cacheKey: string = `examen-download-${this.type}-${this.id}`
  private cacheList: any[] = []
  public testCache: any = !!localStorage.getItem(this.cacheKey)
  public s: any = this.navParams.get('s') ? JSON.parse(this.navParams.get('s')) : null

  public duration: number;
  public duration_label: string;

  public answeredQuestions: string[] = [];
  public lastQuestions: Question[];

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private back: BackProvider,
    private toast: ToastController,
    public viewCtrl: ViewController,
    private modal: ModalController,
    private afs: AngularFirestore,
    private alert: AlertController,
  ) {
  }

  ionViewDidLoad() {

    this.exam$ = this.afs.collection(Collections.EXAM).doc<Exam>(this.id)
      .valueChanges()
      .pipe(
        tap(data => {

          this.test = {name: data.name, preguntas: data.questions}
          if (data.duration) {

            const alert = this.alert.create({
              message: '¿Quieres iniciar el exámen con tiempo?',
              buttons: [
                { text: 'Sí', handler: () => this.handleSetTimer(data.duration * 60 * 1000) },
                { text: 'No', role: 'cancel' }
              ]
            })

            alert.present();

          }

          // Load Cache
          const key = `examen-cache-${this.type}-${this.id}`;
          const cacheStr = localStorage.getItem(key);

          if (cacheStr) {

            const cache = JSON.parse(cacheStr);
            console.log(Object.values(cache.resultados).filter((r: any) => r.selected));

            this.index = cache.index;

            this.test.preguntas = this.test.preguntas.map(q => {

              if (cache.resultados[q.id] && cache.resultados[q.id].selected) {
                q.s = cache.resultados[q.id].selected;
                return q;
              } else {
                return q;
              }

            })

          }

        })
      )

    /* if (this.s) return this.test = this.s.test

    this.back.getClase(this.id, this.type).subscribe(data => {
      if (!data.preguntas || data.preguntas.length <= 0) {
        this.toast.create({message: 'No existe un exámen para ésta clase...', duration: 2000}).present()
        this.viewCtrl.dismiss()
      } else {
        this.test = {name: data.name, preguntas: data.preguntas}
        const cache = JSON.parse(localStorage.getItem(`examen-cache-${this.type}-${this.id}`))
        if (cache) {
          this.index = cache.index
          this.test.preguntas[this.index].s = cache.selected
        }
      }
    }, err => {
      this.toast.create({message: 'No se pudo cargar el exámen...', duration: 2000}).present()
    })

    this.loadCacheList() */

  }

  enviarResultados() {

    this.l = true

    let resultados = {}
    this.test.preguntas.forEach(p => {
      resultados[p.id] = {
        selected: p.s,
        pregunta: p
      }
    })

    if (this.isOffline) {

      const list = JSON.parse(localStorage.getItem('offline-results'))
      const offlineResults = list ? list : []

      offlineResults.push(resultados)
      localStorage.setItem('offline-results', JSON.stringify(offlineResults))

      this.toast.create({message: 'Tus resultados se guardaron correctamente.', duration: 2000}).present()
      this.viewCtrl.dismiss(resultados)

    } else {

      this.back.addResultado(this.type, this.back.uid, this.id, resultados).subscribe(data => {
        this.toast.create({message: 'Resultados registrados correctamente.', duration: 2000}).present()
        this.viewCtrl.dismiss(data)
      }, err => {
        this.l = false
        /* this.toast.create({message: 'No se pudieron agregar tus resultados...', duration: 2000}).present() */
      })

    }

  }

  openResultados(id, type) {
    const m = this.modal.create('ResultadosExamenPage', {type, id})
    m.present()
  }

  next() {
    if (this.index == this.test.preguntas.length - 1) {
      this.enviarResultados()
    } else {
      this.saveCache()
      this.lastQuestions = [this.test.preguntas[this.index]];
      this.index++
      const m = this.modal.create('ExamFeedbackPage', { q: this.lastQuestions, selected: this.test.preguntas[this.index].s });
      m.present();
    }
  }

  saveCache() {

    let resultados = {}
    let selected = this.test.preguntas[this.index].s
    this.test.preguntas.forEach(p => {
      resultados[p.id] = {
        selected: p.s,
        pregunta: p
      }
    })

    localStorage.setItem(`examen-cache-${this.type}-${this.id}`, JSON.stringify({
      index: this.index,
      resultados,
      selected
    }))

    localStorage.setItem(`examen-selected-${this.type}-${this.id}-${this.index}`, this.test.preguntas[this.index].s)

    console.log(this.test.preguntas[this.index])

    if (!this.answeredQuestions.includes(this.test.preguntas[this.index].id)) this.answeredQuestions.push(this.test.preguntas[this.index].id);

  }

  prev() {

    this.index--

    const selected = localStorage.getItem(`examen-selected-${this.type}-${this.id}-${this.index}`)
    if (selected) this.test.preguntas[this.index].s = selected

  }

  openAvance() {

    const m = this.modal.create('AvanceExamenPage', {preguntas: this.test.preguntas, answered: this.answeredQuestions})
    m.present()

    m.onDidDismiss(index => {
      if (!index) return
      this.index = index
    })

  }

  loadCacheList() {
    const list = JSON.parse(localStorage.getItem('zamnademy-cache-list'))
    this.cacheList = list ? list : []
  }

  downloadExamen() {
    const cache = {
      id: this.id,
      type: this.type,
      test: this.test
    }
    this.cacheList.push({
      id: cache.id,
      name: cache.test.name,
      type: cache.type
    })
    localStorage.setItem('zamnademy-cache-list', JSON.stringify(this.cacheList))
    localStorage.setItem(this.cacheKey, JSON.stringify(cache))
    this.testCache = cache
    this.toast.create({message: 'Exámen descargado correctamente.', duration: 2000}).present()
  }

  removeExamen() {
    localStorage.removeItem(this.cacheKey)
    this.cacheList = this.cacheList.filter(test => test.id != this.id)
    localStorage.setItem('zamnademy-cache-list', JSON.stringify(this.cacheList))
    this.testCache = null
  }

  private handleSetTimer(duration: number) {

    this.duration = duration
    console.log('duration', duration);

    let timer = setInterval(() => {
      if (this.duration > 0) this.duration -= 1000

      /*const time_format = this.exam.type == ExamTypes.SIMULACRO ? countdown.HOURS | countdown.MINUTES | countdown.SECONDS : countdown.MINUTES | countdown.SECONDS*/
      const time_format = countdown.HOURS | countdown.MINUTES | countdown.SECONDS;

      this.duration_label = countdown(moment().add(this.duration, 'milliseconds').toDate(), null, time_format)
        .toString()
        .replace(/<small>|<\/small>/g, '')

      /* this.duration_label = moment()
        .startOf('day')
        .seconds(this.duration * 60)
        .format('H:mm:ss') */
    }, 1000)

    setTimeout(() => {
      this.enviarResultados()
      clearInterval(timer)
    }, duration)

  }

}



